<script>
export default {
  name: "ProductDetail",
  // item.isSeoul
  props: {
    item: {
      require: true,
      type: Object,
    },
  },
  methods: {
    expandHandler(item) {
      item.isExpand = !item.isExpand;
    },
  },
  computed: {
    // 
    screenView() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return true;
        case "sm":
          return true;
        case "md":
          return true;
        case "lg":
          return false;
        case "xl":
          return false;
        default:
          return true;
      }
    },
    width() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return undefined;
        case "sm":
          return undefined;
        case "md":
          return undefined;
        case "lg":
          return 750;
        case "xl":
          return 750;
        default:
          return undefined;
      }
    },
  },
};
</script>
